<template>
	
		<view class ="page">
			 
			<navigator  class = "first_page">{{$t('basic')}}</navigator>
			<view class="box4">
			<view class="box3">
		
				<view class= "box3_i1"><image class=icon src="../../static/温度.png">
				<text class= "box3_text1">{{temperature}}</text>
				</image></view>
				<view class= "box3_i1"><image class=icon src="../../static/酸值软测量.png"></image>
				<text class= "box3_text1">{{ph}}</text>
				</view>
		
				<view class= "box3_i1"><image class=icon src="../../static/溶氧.png"></image>
				<text class= "box3_text1">{{oxygen}}</text>
				</view>	
				
			</view>
			<view class="box3">
				
				<view class= "box3_i1">
				<text class= "box3_text2">{{$t('temp')}}( °C )</text>
				</view>
				<view class= "box3_i1">
				<text class= "box3_text2">{{$t('ph')}}</text>
				</view>
					
				<view class= "box3_i1">
				<text class= "box3_text2">{{$t('oxygen')}}(mg/L)</text>
				</view>	
				
			</view>
			</view>
			
			<view class="box1" @click="n1">
				<view class="icon1"><image class=icon src="../../static/走势.png"></image></view>
				<view class="i2">{{$t('history_chart')}}</view>
			</view>
			
			<view>
				<scroll-view :scroll-top="scrollTop" scroll-y="true" class="scroll-Y" @scrolltoupper="upper"
						@scrolltolower="lower" @scroll="scroll">
					<view class="box2">
						<view class = "j1"><image class=icon src="../../static/高温报警.png"></image></view>
						<view class="j2">{{$t('alarm_temp')}}</view>
						<view class="j3"><switch></switch></view>
					</view>
					<view class="box1">
						<view class = "j1"><image class=icon src="../../static/上箭头.png"></image></view>
						<view class="j2" @click="alarm_set('hTemper','温度报警','高温预警值设定')" >{{$t('alarm_temp_h')}}</view>
						<view class="j3">{{hTemper}}°C</view>
					</view>
					<view class="box1">
						<view class = "j1"><image class=icon src="../../static/下箭头.png"></image></view>
						<view class="j2" @click="alarm_set('lTemper','温度报警','低温预警值设定')">{{$t('alarm_temp_l')}}</view>
						<view class="j3">{{lTemper}}°C</view>
					</view>
					<view class="box2">
						<view class = "j1"><image class=icon src="../../static/报警,警报,报警灯,危险.png"></image></view>
						<view class="j2" >{{$t('alarm_ph')}}</view>
						<view class="j3"><switch></switch></view>
					</view>
					<view class="box1">
						<view class = "j1"><image class=icon src="../../static/上箭头.png"></image></view>
						<view class="j2" @click="alarm_set('hPh','PH数值报警','高位预警值设定')">{{$t('alarm_ph_h')}}</view>
						<view class="j3">{{hPh}}</view>
					</view>
					<view class="box1">
						<view class = "j1"><image class=icon src="../../static/下箭头.png"></image></view>
						<view class="j2" @click="alarm_set('lPh','PH数值报警','低位预警值设定')">{{$t('alarm_ph_l')}}</view>
						<view class="j3">{{lPh}}</view>
					</view>
					
					<view class="box2">
						<view class = "j1"><image class=icon src="../../static/感叹号-圈.png"></image></view>
						<view class="j2">{{$t('alarm_oxygen')}}</view>
						<view class="j3"><switch></switch></view>
					</view>
					<view class="box1">
						<view class = "j1"><image class=icon src="../../static/上箭头.png"></image></view>
						<view class="j2" @click="alarm_set('hOxygen','溶解氧浓度报警','高位预警值设定')">{{$t('alarm_oxygen_h')}}</view>
						<view class="j3">{{hOxygen}}mg/L</view>
					</view>
					<view class="box1">
						<view class = "j1"><image class=icon src="../../static/下箭头.png"></image></view>
						<view class="j2" @click="alarm_set('lOxygen','溶解氧浓度报警','低位预警值设定')">{{$t('alarm_oxygen_l')}}</view>
						<view class="j3">{{lOxygen}}mg/L</view>
					</view>	
				</scroll-view>
			</view>
			<view class="copyright_text" >
				<view class = "text1"><text>{{$t('copyright')}}</text></view>
				<view class = "text1"><text>{{$t('school')}}</text></view>
				<view class = "text1"><text>{{$t('organization')}}</text></view>
			</view>
			
			
				
		</view>	
		
				
			
			
			
		
		
	
</template>

<script>
	
	import {
		g_id,
		g_ph,
		g_oxygen,
		g_temperature
	} from "../../m_Common/m_common.js"
	export default {
		
		data() {
			return {
				
				hTemper:0,
				lTemper:0,
				hPh:0,
				lPh:0,
				hOxygen:0,
				lOxygen:0,
				data:"",
				id:"",
				ph:"",
				temperature:"",
				oxygen:"",
				show:false,
				scrollTop: 0,
				old: {
					scrollTop: 0,
				}
			}
		},
	
		onLoad() {
			var data = getApp().globalData.json_data;
			this.hTemper=data.h_temp;
			this.lTemper=data.l_temp;
			this.hPh=data.h_ph;
			this.lPh=data.l_ph;
			this.hOxygen=data.h_oxygen;
			this.lOxygen=data.l_oxygen;
			getApp().watch(this.getTemperature,'g_temperature')
			getApp().watch(this.getId,'g_id')
			getApp().watch(this.getOxygen,'g_oxygen')
			getApp().watch(this.getPh,'g_ph')
			var str = "数字渔场环境监控系统("+data.district+"观测点)"
			  uni.setNavigationBarTitle({
			    title:str
			});
		},
		
		methods: {
		    getTemperature(val){
		        this.temperature = val
		    },
			getId(val){
			    this.id = val
			},
			getPh(val){
			    this.ph = val
			},
			getOxygen(val){
			    this.oxygen = val
			},
			alarm_post(attr,data){
				var d = getApp().globalData.json_data;
				uni.request({
						url: 'http://47.100.200.181:8000/attr_set', // 路径
						method: 'POST', // 请求方法
						header: {
						    'content-type': 'application/x-www-form-urlencoded'
						} ,
						data: {
							symbol:d.symbol,
							attr : attr,
							data : data,
						}, //发送的数据
					})
			},
			alarm_set(attribute,titletext,placeholdertext){
				var that = this
				uni.showModal({
					title: titletext,
					cancelColor: "#FF0606",
					placeholderText: placeholdertext,
					content: '',
					editable: true,
					success: function (res) {
					if (res.confirm) {
						console.log('用户点击确定');
						if(attribute == "hTemper"){
							that.hTemper = res.content;
							getApp().globalData._hTemper = res.content;
							that.alarm_post('h_temp',res.content)
						}else if(attribute=="lTemper"){
							that.lTemper = res.content;
							getApp().globalData._lTemper = res.content;
							that.alarm_post('l_temp',res.content)
							
						}else if(attribute == "hPh"){
							that.hPh = res.content;
							getApp().globalData._hPh = res.content;
							that.alarm_post('h_ph',res.content)
						}else if(attribute == "lPh"){
							that.lPh = res.content
							getApp().globalData._lPh = res.content;
							that.alarm_post('l_ph',res.content)
						}else if(attribute == "hOxygen"){
							that.hOxygen = res.content
							getApp().globalData._hOxygen = res.content;
							that.alarm_post('h_oxygen',res.content)
						}else if(attribute == "lOxygen"){
							that.lOxygen = res.content
							getApp().globalData._lOxygen = res.content;
							that.alarm_post('l_oxygen',res.content)
						}
						
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
					
				})
			},
			n1(){
				uni.navigateTo({
					url:"/pages/index/chart_xianxiang/chart_xianxiang"
				})
			},
			
			upper: function(e) {
							console.log(e)
							
						},
			lower: function(e) {
							console.log(e)
						},
			scroll: function(e) {
							console.log(e)
							this.old.scrollTop = e.detail.scrollTop
						},
			goTop: function(e) {
							// 解决view层不同步的问题
							this.scrollTop = this.old.scrollTop
							this.$nextTick(function() {
								this.scrollTop = 0
							});
							uni.showToast({
								icon: "none",
								title: "纵向滚动 scrollTop 值已被修改为 0"
							})
						},
		
		}
	}
</script>

